import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTheme } from '@mui/material/styles';
import { styled } from '@mui/material/styles';

// material-ui
import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, Card, IconButton, Typography, Menu, MenuItem, CardContent, Button, TextField, TableContainer, TableHead, TableRow, Tooltip, Fab } from '@mui/material';

import { useDispatch, useSelector } from 'store';
//Icons
import DeleteIcon from '@mui/icons-material/Delete'
// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';

// third-party
import { useFormik } from 'formik';
import EditAttributesSharp from '@mui/icons-material/Edit';

//redux
import { SaveInterimInvoice } from 'store/slices/technicalteam';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        minWidth: 200
    }
}));

const StyledTableCellGroup = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
    },
    // hide last border
    '&:last-of-type td, &:last-of-type th': {
        border: 0
    }
}));


export default function TableInfo({ paymentData, handleNext, handleBack }) {
    const formik = useFormik({
        initialValues: {
            mainContractor: paymentData.mainContractor,
        },
        onSubmit: (values) => {
            SaveTableData();
            handleNext();
        }
    });
    const dispatch = useDispatch();

    const [AddtableData, setAddtable] = useState([]);

    const Addtablerow = () => {
        const newRow = { id: AddtableData.length + 1, totalValue: "", DTotal: "", DComplete: "", DCurrent: "", DPrevious: "", ITotal: "", IComplete: "", ICurrent: "", IPrevious: "", STotal: "", SComplete: "", SCurrent: "", SPrevious: "", price: "", unit: "", description: "", clause: "" };
        setAddtable([...AddtableData, newRow]);
    }

    const handleInputChange = (id, column, value) => {
        const updatedData = AddtableData.map((row) =>
            row.id === id ? { ...row, [column]: value } : row
        );
        setAddtable(updatedData);
    };

    const DeleteRowData = (id) => () => {
        setAddtable(AddtableData.filter((row) => row.id != id));
    }

    const SaveTableData = async () => {
        await dispatch(SaveInterimInvoice(AddtableData));
    }

    return (
        <>
            <form onSubmit={formik.handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <TableContainer>
                            <Table sx={{ minWidth: 350 }} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell></TableCell>
                                        <StyledTableCellGroup align='center' colSpan={4}>Delivery Consultant</StyledTableCellGroup>
                                        <TableCell align='center' colSpan={4}>Installation</TableCell>
                                        <StyledTableCellGroup align='center' colSpan={4}>Supply</StyledTableCellGroup>
                                        <TableCell align='center' colSpan={5}></TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align='center'>Total Value</TableCell>
                                        <TableCell align='center'>Total</TableCell>
                                        <TableCell align='center'>Completed</TableCell>
                                        <TableCell align='center'>Current</TableCell>
                                        <TableCell align='center'>Prevoius</TableCell>
                                        <TableCell align='center'>Total</TableCell>
                                        <TableCell align='center'>Completed</TableCell>
                                        <TableCell align='center'>Current</TableCell>
                                        <TableCell align='center'>Prevoius</TableCell>
                                        <TableCell align='center'>Total</TableCell>
                                        <TableCell align='center'>Completed</TableCell>
                                        <TableCell align='center'>Current</TableCell>
                                        <TableCell align='center'>Prevoius</TableCell>
                                        <TableCell align='center'>Price (EGP)</TableCell>
                                        <TableCell align='center'>Unit</TableCell>
                                        <StyledTableCell align='center'>Description</StyledTableCell>
                                        <TableCell align='center'>Clause Number</TableCell>
                                        <TableCell align='center'>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {AddtableData.map((row) => (
                                        <TableRow hover key={row.id}>
                                            {Object.keys(row).map((item, i) => (
                                                <>
                                                    {item != "id" && (
                                                        <TableCell align="center" key={i}>
                                                            <TextField
                                                                value={row[item]}
                                                                onChange={(e) => handleInputChange(row.id, item, e.target.value)}
                                                                id="standard-basic" variant="standard"
                                                            />
                                                        </TableCell>
                                                    )}
                                                </>
                                            ))}

                                            <TableCell align="center" sx={{ pr: 3 }}>
                                                <IconButton onClick={DeleteRowData(row.id)} size="large">
                                                    <DeleteIcon
                                                        fontSize="small"
                                                        aria-controls="menu-popular-card-1"
                                                        aria-haspopup="true"
                                                        sx={{ color: 'grey.500' }}
                                                    />
                                                </IconButton>
                                            </TableCell>

                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack direction="row" justifyContent="space-between">
                            <Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
                                Back
                            </Button>
                            <AnimateButton>
                                <Button variant="contained" sx={{ my: 3, ml: 1 }} onClick={Addtablerow}>
                                    AddTable
                                </Button>
                                <Button variant="contained" type="submit" sx={{ my: 3, ml: 1 }}>
                                    Next
                                </Button>
                            </AnimateButton>
                        </Stack>
                    </Grid>
                </Grid>
            </form>
        </>
    );
}

TableInfo.propTypes = {
    paymentData: PropTypes.object,
    setPaymentData: PropTypes.func,
    handleNext: PropTypes.func,
    handleBack: PropTypes.func,
    setErrorIndex: PropTypes.func
};
