import React from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Button, Checkbox, FormControlLabel, Grid, Stack, Typography, TextField } from '@mui/material';

// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';

// third-party
import { useFormik } from 'formik';
import * as yup from 'yup';

const validationSchema = yup.object({
    mainContractor: yup.string().required('Main Contractor is required'),
});

// ==============================|| FORM WIZARD - VALIDATION  ||============================== //

const BasicInfo = ({ shippingData, setShippingData, handleNext, setErrorIndex }) => {
    const formik = useFormik({
        initialValues: {
            mainContractor: shippingData.mainContractor,
        },
        validationSchema,
        onSubmit: (values) => {
            setShippingData({
                mainContractor: values.mainContractor,
            });
            handleNext();
        }
    });

    const [valueBasic, setValueBasic] = React.useState(new Date());

    return (
        <>
            <form onSubmit={formik.handleSubmit} id="validation-forms">
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            id="mainContractor"
                            name="mainContractor"
                            label="Main Contractor *"
                            value={formik.values.mainContractor}
                            onChange={formik.handleChange}
                            error={formik.touched.mainContractor && Boolean(formik.errors.mainContractor)}
                            helperText={formik.touched.mainContractor && formik.errors.mainContractor}
                            fullWidth
                            autoComplete="main-contractor"
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="contractor" name="contractor" label="Contractor" fullWidth autoComplete="contractor" />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="commericalRegistration" name="commericalRegistration" label="Commerical Registration" fullWidth autoComplete="commericalRegistration" />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <DateTimePicker
                                renderInput={(props) => <TextField fullWidth {...props} helperText="" />}
                                label="Date & Time"
                                value={valueBasic}
                                onChange={(newValue) => {
                                    setValueBasic(newValue);
                                }}
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="taxNumber" name="taxNumber" label="Tax Number" fullWidth autoComplete="taxNumber" />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="businessScope" name="businessScope" label="Business Scope" fullWidth />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="project" name="project" label="Project" fullWidth autoComplete="project" />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="interim" name="interim" label="Interim" fullWidth autoComplete="interim" />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField id="interimInvoiceNo" name="interimInvoiceNo" label="Interim Invoice No" fullWidth autoComplete="interimInvoiceNo" />
                    </Grid>
                    <Grid item xs={12}>
                        <Stack direction="row" justifyContent="flex-end">
                            <AnimateButton>
                                <Button variant="contained" sx={{ my: 3, ml: 1 }} type="submit" onClick={() => setErrorIndex(0)}>
                                    Next
                                </Button>
                            </AnimateButton>
                        </Stack>
                    </Grid>
                </Grid>
            </form>
        </>
    );
};

BasicInfo.propTypes = {
    shippingData: PropTypes.object,
    setShippingData: PropTypes.func,
    handleNext: PropTypes.func,
    setErrorIndex: PropTypes.func
};

export default BasicInfo;
